	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://cl.ly/0w3j30140G45/functional-javascript-cover.jpg\" alt=\"Functional Javascript Cover\"></p>\n<p><a href=\"http://aladin.kr/p/rUEvd\">책 링크</a></p>\n<p>일단 자바스크립트 초급자가 읽기 좋은 책은 아니다. 하지만 자바스크립트를 좀 다루면서, Redux 나 RxJS 같은 라이브러리를 접해본 사람들이\n'왜 이런 방식으로 코드를 작성해야하는지' 단서를 얻을 수도 있고, 더 나아가서 함수형 프로그래밍의 기본 개념을 자신에게 익숙한 자바스크립트로 익힐 수 있다.</p>\n<p>특히 5 장 때문에 이 책을 활용한 단기 스터디를 진행했는데, 확실히 팀원에게 내가 생각하는 함수 분할 및 조합에 대한 생각을 공유하고 이해시키는데 많은 도움이 되었다.</p>\n<p>번역은 자잘한 오타와 바로 이해가 되기 어려운 내용이 일부 있지만, 최근에 본 다른 책의 번역에 비하면 훨씬 나은 편이다.</p>\n<p>개인적으로 국내 저서 '함수형 자바스크립트 프로그래밍(유인동 저)' 보다는 이 책을 먼저 추천하고 싶다.\n국내 저서도 충분히 기본적인 내용을 소개하고 직접 유틸리티 함수를 구현해보는 방식의 접근을 취하고 있지만,\n2017 년에 나오는 책이 ES6 문법을 메인으로 채택하고 있지 않는 이유를 모르겠다.\n또한 정말 본질적으로 궁금했던 Functor 와 Monad 의 개념 및 활용법, 그리고 비동기 함수를 다루는 방법에 대해서는 명시적으로 드러내고 있지 않아서 약간 아쉬웠다.</p>\n<p>다행히도 이 책은 아쉬웠던 부분을 어느 정도 다루고 있기 때문에 나처럼 국내 저서에 그닥 만족스럽지 못했던 사람에게는 그래도 조금 더 나은 책이 될 것이다.</p>\n<hr>\n<p>(2018–03–22 추가) 국내 저서 ‘함수형 자바스크립트 프로그래밍’ 의 저자 유인동님이 지금 읽고 계시는 리뷰에 대해  <a href=\"https://medium.com/@indongyoo/%25EC%2595%2588%25EB%2585%2595%25ED%2595%2598%25EC%2584%25B8%25EC%259A%2594-%25EC%259C%25A0%25EC%259D%25B8%25EB%258F%2599%25EC%259E%2585%25EB%258B%2588%25EB%258B%25A4-%25EC%259D%2598%25EA%25B2%25AC-%25EB%2582%25A8%25EA%25B2%25A8%25EC%25A3%25BC%25EC%2585%2594%25EC%2584%259C-%25EA%25B3%25A0%25EB%25A7%2599%25EC%258A%25B5%25EB%258B%2588%25EB%258B%25A4-b33a8c73b582\">답변을 달아주셨습니다</a>. 꼭 읽어보시길 권합니다. 또한 인동님이 최근에 <a href=\"https://github.com/Functional-JavaScript/functional.es\">Github 에 ES6 에 초점을 맞춘 내용으로 다른 글을 작성하고 계시니</a>  참고하시면 많은 도움이 되리라 생각합니다.</p>","frontmatter":{"date":"2018/03/14","path":"/posts/functional-javascript-review","title":"함수형 자바스크립트(루이스 아텐시오 저) 리뷰","tags":["Functional Programming","Javascript"]}},"site":{"siteMetadata":{"siteUrl":"https://emaren84.github.io","disqusShortUrl":"gatsby-blog-1"}}},"pathContext":{}}

